import React from 'react';
import {Link} from 'react-router';

export default function(props) {

  return (
    <div id="wrapper" className={`${props.sidebarToggle ? 'toggled' : '' }`} >
     <div id="sidebar-wrapper">
         <ul className="sidebar-nav">
             <li className="sidebar-brand">
                 <a href="#">
                     Start Bootstrap
                 </a>
             </li>
             <li>
                 <div >
                   <h4 onClick={() => { props.toggleClick('boardTemplateToggle'); }}>
                     <div>Template</div>
                   </h4>
                   { props.boardTemplateToggle ? (
                     <label style={{paddingLeft: '5%'}}>Toggling</label>
                     ) : null }

                 </div>
             </li>
             <li>
                 <a href="#">Shortcuts</a>
             </li>
         </ul>
     </div>
     <div id="page-content-wrapper">
         <div >
             <div className="row">
                 <div className="col-lg-12">
                     <button className="ion-gear-b" style={{fontSize: '2em'}} type='button' onClick={() => { props.toggleClick('sidebarToggle'); }}></button>
                 </div>
             </div>
         </div>
     </div>
   </div>
  );
}


